/* Center the main login form */
.main-area {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px;
}


.login form {
    width: 400px;
    margin: 0 auto;
    padding: 50px;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

/* Title */
.login h2 {
    margin-bottom: 20px;
    text-align: center;
    color: #007BFF;
}

/*  input */
.input-line {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.input-line label {
    width: 80px;
    text-align: center;
    font-weight: bold;
    font-size: small;
    margin-right: 10px;
}

.input-line input {
    flex: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

/* 로그인 버튼 */
.button-wrapper-login {
    align-items: center;
    justify-content: center;
    display: flex;
    margin-top: 20px;
}

#login {
    width: 100px;
    min-width: fit-content;
    padding: 12px;
    background-color: #007BFF;
    color: white;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    font-size: 16px;
    cursor: pointer;
}

#login:hover {
    background-color: #0056b3;
}

/* 하단 links */
.small-links {
    text-align: center;
    margin-top: 20px;
    font-size: 12px;
}

.small-links a {
    color: #007BFF;
    text-decoration: none;
    margin: 0 5px;
}

.small-links a:hover {
    text-decoration: underline;
}



